:root {
  --bg: #0b0f14;
  --panel: rgba(255, 255, 255, 0.06);
  --panel-2: rgba(255, 255, 255, 0.10);
  --text: rgba(255, 255, 255, 0.92);
  --muted: rgba(255, 255, 255, 0.65);
  --border: rgba(255, 255, 255, 0.10);
  --accent: #ff6a1a; /* laranja */
  --accent-2: #ff8747;
}

* {
  box-sizing: border-box;
}

html,
body {
  padding: 0;
  margin: 0;
  height: 100%;
  background: radial-gradient(1200px 600px at 15% 10%, rgba(255, 106, 26, 0.18), transparent 60%),
              radial-gradient(900px 500px at 80% 20%, rgba(255, 135, 71, 0.12), transparent 55%),
              var(--bg);
  color: var(--text);
  font-family: system-ui, -apple-system, Segoe UI, Roboto, Arial, sans-serif;
}

a {
  color: inherit;
  text-decoration: none;
}

.layout {
  display: grid;
  grid-template-columns: 260px 1fr;
  min-height: 100vh;
}

.content {
  padding: 28px;
}

.container {
  max-width: 1100px;
  margin: 0 auto;
}

.h1 {
  font-size: 28px;
  margin: 0 0 14px 0;
  letter-spacing: 0.2px;
}

.h2 {
  font-size: 18px;
  margin: 22px 0 12px 0;
  color: var(--text);
}

.muted {
  color: var(--muted);
}

.card {
  background: var(--panel);
  border: 1px solid var(--border);
  border-radius: 18px;
  padding: 16px;
  backdrop-filter: blur(10px);
}

.grid {
  display: grid;
  gap: 14px;
}

.grid-3 {
  grid-template-columns: repeat(3, minmax(0, 1fr));
}

@media (max-width: 1100px) {
  .layout {
    grid-template-columns: 220px 1fr;
  }
  .grid-3 {
    grid-template-columns: repeat(2, minmax(0, 1fr));
  }
}

@media (max-width: 760px) {
  .layout {
    grid-template-columns: 1fr;
  }
  .content {
    padding: 18px;
  }
  .grid-3 {
    grid-template-columns: 1fr;
  }
}

/* “linha horizontal” para próximos eventos */
.hscroll {
  display: grid;
  grid-auto-flow: column;
  grid-auto-columns: minmax(260px, 1fr);
  gap: 14px;
  overflow-x: auto;
  padding-bottom: 6px;
}

.hscroll::-webkit-scrollbar {
  height: 8px;
}
.hscroll::-webkit-scrollbar-thumb {
  background: rgba(255, 255, 255, 0.12);
  border-radius: 999px;
}

/* botão */
.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;

  background: linear-gradient(180deg, var(--accent), #ff5a00);
  color: #0b0f14;
  border: none;
  border-radius: 14px;
  padding: 12px 14px;
  font-weight: 700;
  cursor: pointer;
}

.btn:active {
  transform: translateY(1px);
}

.badge {
  display: inline-flex;
  align-items: center;
  padding: 6px 10px;
  border-radius: 999px;
  border: 1px solid var(--border);
  background: rgba(255, 255, 255, 0.06);
  color: var(--muted);
  font-size: 12px;
}

/* footer */
.footer {
  margin-top: 24px;
  padding: 18px;
  border-radius: 18px;
  background: var(--panel);
  border: 1px solid var(--border);
}
